import React from 'react';
import CustomParallax from '../components/CustomParallax'
import m1 from '../assets/m1.jpg'
import m2 from '../assets/m2.jpg'
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";

export default function Readme() {
    return (
        <>
            {/* <CustomParallax title='Readme' text="Nitai Aharoni Readme" img={home_top} height='20em'/> */}
            {/* <Container className="p-5"> */}
                <Image width="100%" className="img-thumbnail mt-1" src={m2} alt="menu 2"/>
                <Image width="100%" className="img-thumbnail mt-1" src={m1} alt="menu 1"/>
            {/* </Container> */}
        </>
    );
}